<template>
	<div class="weui-panel weui-panel_access">
	    
	    <slot name="title"></slot>
	    <div class="weui-panel__bd">
	        <a href="javascript:void(0);" class="weui-media-box weui-media-box_appmsg" v-for="item in itemlst">
	
					<div class="weui-media-box__hd">
						<img class="weui-media-box__thumb" src="data:image/png;base64," alt="">
					</div>
					<router-link :to="{path:'/diaohuo/detail' , query:{id:item.id} }"  >
						<div class="weui-media-box__bd">
							<h4 class="weui-media-box__title">{{ item.title }}</h4>
							<p class="weui-media-box__desc">{{ item.content }}</p>
						</div>					
					</router-link>
	        </a>
	    </div>
	</div>
</template>

<script>
import $ from 'n-zepto'
    export default {
    	data(){
            return{

            }
        },
		props: ['itemlst'],
	    created: function () {

		},
        methods: {

        }
    }
</script>

<style >
.weui-panel {
    background-color: #fff;
    margin-top: 2px;
    position: relative;
    overflow: hidden;
}
</style>



